import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { FormsModule } from '@angular/forms';
import { NgModule } from '@angular/core';
import { ReactiveFormsModule } from '@angular/forms';
import { GridModule } from '@progress/kendo-angular-grid';
import { DropDownListModule } from '@progress/kendo-angular-dropdowns';
import { ComponentFactoryComponent } from './component-factory.component';
import { BasicinfocardComponent } from '../component/basicinfo-card/basicinfocard.component';
import { MethodparamListComponent } from '../component/methodparam-list/methodparam-list.component';
import { ReturnInfoCardComponent } from '../component/return-info-card/return-info-card.component';
import { BizEntityDesignerComponent } from '../component-designer/biz-entity-designer/biz-entity-designer.component';
import { BeMgrDesignerComponent } from '../component-designer/be-mgr-designer/be-mgr-designer.component';
import { DeterminationDesignerComponent } from '../component-designer/determination-designer/determination-designer.component';
import { ValidationDesignerComponent } from '../component-designer/validation-designer/validation-designer.component';
import { ViewModelDesignerComponent } from '../component-designer/view-model-designer/view-model-designer.component';

@NgModule({
  declarations: [
    ComponentFactoryComponent,
    BasicinfocardComponent,
    MethodparamListComponent,
    ReturnInfoCardComponent,
    BizEntityDesignerComponent,
    BeMgrDesignerComponent,
    DeterminationDesignerComponent,
    ValidationDesignerComponent,
    ViewModelDesignerComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    FormsModule,
    GridModule,
    ReactiveFormsModule,
    DropDownListModule
  ],
  providers: [],
  exports: [ComponentFactoryComponent]
})
export class ComponentFactoryModule { }



