import { Component, OnInit, Input } from '@angular/core';
import { ParameterTypes, ParameterType } from '../../../assets/enum/ParameterType';
import { ReturnValue } from '../../../assets/libs/BaseEntity/ReturnValue';

@Component({
  selector: 'app-return-info-card',
  templateUrl: './return-info-card.component.html',
  styleUrls: ['./return-info-card.component.css']
})
/**
 * 组件：返回值卡片
 */
export class ReturnInfoCardComponent implements OnInit {
  /**
   * 返回值参数类型
   */
  private paramTypes = ParameterTypes;
  /**
   * 输入的返回值实体
   */
  @Input() returnValue: ReturnValue;
  /**
   * 返回值类型Index，默认是String类型
   */
  private selectedIndex = 0;

  constructor() { }

  ngOnInit() {
    this.selectedIndex = this.getParamTypeIndex(this.returnValue.ParamType);
  }
  private getParamTypeIndex(type: ParameterType): number {
    return this.paramTypes.find(item => item.value === type).index;
  }
  /**
   * 事件：返回值类型切换事件
   * @param selectedIndex 返回值类型集合中的索引
   */
  selectType(selectedIndex: number) {
    this.selectedIndex = selectedIndex;
    this.returnValue.Assembly = this.getReturnValueAssembly(selectedIndex);
    this.returnValue.ClassName = this.getReturnValueClassName(selectedIndex);
  }
  getReturnValueAssembly(selectedIndex: number): string {
    const assembly = this.paramTypes.find(item => item.index === selectedIndex).assembly;
    return assembly ? assembly : '';
  }
  getReturnValueClassName(selectedIndex: number): string {
    const className = this.paramTypes.find(item => item.index === selectedIndex).className;
    return className ? className : '';
  }
}



