import { Component, OnInit, Input } from '@angular/core';
import { GSPComponent } from '../../assets/libs/BaseEntity/GSPComponent';
import { BEComponent } from '../../assets/libs/BEComponent/BEComponent';
import { BEMgrComponent } from '../../assets/libs/BEMgrComponent/BEMgrComponent';
import { ValidationComponent } from '../../assets/libs/ValidationComponent/ValidationComponent';
import { DeterminationComponent } from '../../assets/libs/DeterminationComponent/DeterminationComponent';
import { VMComponent } from '../../assets/libs/VMComponent/VMComponent';

@Component({
  selector: 'app-component-factory',
  templateUrl: './component-factory.component.html',
  styleUrls: ['./component-factory.component.css']
})
export class ComponentFactoryComponent implements OnInit {
  /**
   * 构件基本信息（由构件设计器传入）
   */
  @Input() baseCmpInfo: any;
  /**
   * 序列化后的构件实体（修改构件实体时传入）
   */
  @Input() strComponent: string;
  /**
   * 作用：
   * 1、本地调用：直接传入构件实体
   * 2、构件设计器传入：作为构件实体的载体
   */
  @Input() component: GSPComponent;
  /** 构件类型 */
  strComponentType: String;

  isBe: Boolean;
  isBeMgr: Boolean;
  isDetermination: Boolean;
  isValidation: Boolean;
  isViewModel: Boolean;

  constructor() { }

  ngOnInit() {
    if (this.baseCmpInfo !== undefined) {
      this.strComponentType = this.baseCmpInfo.ComponentType;
      this.operateComponent();
    } else {
      if (this.component !== undefined) {
        this.strComponentType = this.component.ComponentType; // 后续可删掉，只用来内部测试
        this.isBe = true; // TODO:先举例
      }
    }
  }
  /**
   * 根据操作类型实例化构件实体
   */
  private operateComponent() {
    if (this.baseCmpInfo.operationType === 'add') {
      switch (this.strComponentType) {
        case 'BEComponent':
          this.component = new BEComponent();
          this.isBe = true;
          break;
        case 'BEMgrComponent':
          this.component = new BEMgrComponent();
          this.isBeMgr = true;
          break;
        case 'ValidationComponent':
          this.component = new ValidationComponent();
          this.isValidation = true;
          break;
        case 'DeterminationComponent':
          this.component = new DeterminationComponent();
          this.isDetermination = true;
          break;
        case 'VMComponent':
          this.component = new VMComponent();
          this.isViewModel = true;
          break;
        default:
          alert('当前新建构件类型为：' + this.baseCmpInfo.ComponentType + '，请检查！');
          break;
      }
      this.component.ComponentID = this.baseCmpInfo.id;
      this.component.ComponentCode = this.baseCmpInfo.code;
      this.component.ComponentName = this.baseCmpInfo.name;
    }
    if (this.baseCmpInfo.operationType === 'edit') {
      switch (this.strComponentType) {
        case 'BEComponent':
          this.component = JSON.parse(this.strComponent) as BEComponent;
          this.isBe = true;
          break;
        case 'BEMgrComponent':
          this.component = JSON.parse(this.strComponent) as BEMgrComponent;
          this.isBeMgr = true;
          break;
        case 'ValidationComponent':
          this.component = JSON.parse(this.strComponent) as ValidationComponent;
          this.isValidation = true;
          break;
        case 'DeterminationComponent':
          this.component = JSON.parse(this.strComponent) as DeterminationComponent;
          this.isDetermination = true;
          break;
        case 'VMComponent':
          this.component = JSON.parse(this.strComponent) as VMComponent;
          this.isViewModel = true;
          break;
        default:
          alert('当前修改构件类型为：' + this.baseCmpInfo.ComponentType + '，请检查！');
          break;
      }
    }
  }
}



