import { Component, Input, ViewChild, Renderer2, OnInit, Output, EventEmitter } from '@angular/core';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { AddEvent, GridComponent, CellClickEvent } from '@progress/kendo-angular-grid';
import { ParameterTypes, ParameterType } from '../../../assets/enum/ParameterType';
import { ParameterModes, ParameterMode } from '../../../assets/enum/ParameterMode';
import { Guid } from '../../../assets/utils/Guid';
import { CompParameter } from '../../../assets/libs/BaseEntity/CompParameter';
import { CellCloseEvent } from '@progress/kendo-angular-grid/dist/es2015/editing/cell-close-event';
import { BEMethodParameter } from '../../../assets/libs/BEComponent/BEMethodParameter';
import { BEMgrMethodParameter } from '../../../assets/libs/BEMgrComponent/BEMgrMethodParameter';
import { ValidationParameter } from '../../../assets/libs/ValidationComponent/ValidationParameter';
import { DeterminationParameter } from '../../../assets/libs/DeterminationComponent/DeterminationParameter';
import { VMMethodParameter } from '../../../assets/libs/VMComponent/VMMethodParameter';

const hasClass = (el, className) => new RegExp(className).test(el.className);
const isChildOf = (el, className) => {
  while (el && el.parentElement) {
    if (hasClass(el.parentElement, className)) {
      return true;
    }
    el = el.parentElement;
  }
  return false;
};
@Component({
  selector: 'app-methodparam-list',
  templateUrl: './methodparam-list.component.html',
  styleUrls: ['./methodparam-list.component.css']
})
/**
 * 组件：方法参数列表
 */
export class MethodparamListComponent implements OnInit {
  //#region 数据通信
  @Input() compType: string;
  /** 输入值：参数列表数据源 */
  @Input() parameters: CompParameter[];
  /** 输出值：外部通过paramsResult获取参数集合信息。在保存saveRow()时会触发该事件 */
  @Output() paramsResult: EventEmitter<CompParameter[]> = new EventEmitter<CompParameter[]>();
  /** 监听当前列表Grid组件 */
  @ViewChild(GridComponent) private gridControl: GridComponent;
  //#endregion

  /** 当前活动的参数数据 */
  private paramGroup: FormGroup;
  /** 当前焦点行的行号 */
  private editedRowIndex: number;

  /** 枚举类：参数模型 */
  private paramModes = ParameterModes;
  /** 枚举类：参数类型 */
  private paramTypes = ParameterTypes;

  constructor(private formBuilder: FormBuilder, private renderer: Renderer2) { }

  public ngOnInit() {
    // 用来监听Grid控件，当焦点不在参数列表控件中时，使已有焦点的列表单元格丢失焦点，并保存参数列表数据
    // k-grid-content表示列表的内容，其子类包括各个列表单元格；
    // k-grid-toolbar表示列表边框工具栏，其子类为按钮（toolbar的header不属于其子类）
    // TODO:过滤条件需要再完善
    this.renderer.listen('document', 'click', ({ target }) => {
      if (!isChildOf(target, 'k-grid-content') && !isChildOf(target, 'k-grid-toolbar')) {
        this.gridControl.closeCell();
        this.saveClick();
      }
    });
  }
  /**
   * 获取参数模式值
   */
  private getParamMode(mode: any): string {
    return this.paramModes.find(item => item.value === mode).text;
  }
  /**
   * 获取参数类型值
   */
  private getParamType(type: any): string {
    return this.paramTypes.find(item => item.value === type).text;
  }
  private GetParamAssemblyDisplay(dataItem: CompParameter): string {
    if (dataItem.ParamType === ParameterType.Custom) {
      return dataItem.Assembly;
    } else {
      return this.paramTypes.find(item => item.value === dataItem.ParamType).assembly;
    }
  }
  private GetParamClassNameDisplay(dataItem: CompParameter): string {
    if (dataItem.ParamType === ParameterType.Custom) {
      return dataItem.ClassName;
    } else {
      return this.paramTypes.find(item => item.value === dataItem.ParamType).className;
    }
  }
  /**
   * 获取参数类型对应的程序集名
   * @param type 参数类型
   */
  private getParamAssembly(type: ParameterType): string {
    if (type === ParameterType.Custom) {
      return '';
    } else {
      return this.paramTypes.find(item => item.value === type).assembly;
    }
  }
  /**
   * 获取参数类型对应的类名
   * @param type 参数类型
   */
  private getParamClassName(type: ParameterType): string {
    if (type === ParameterType.Custom) {
      return '';
    } else {
      return this.paramTypes.find(item => item.value === type).className;
    }
  }
  /**
   * 创建一条构件方法的新参数
   */
  private createNewParameter(): CompParameter {
    const newParam = this.initCompParameter(); // 需抽为公共
    newParam.ID = Guid.newGuid();
    newParam.Mode = ParameterMode.IN;
    const stringType = ParameterType.String;
    newParam.ParamType = stringType;
    newParam.Assembly = this.getParamAssembly(stringType);
    newParam.ClassName = this.getParamClassName(stringType);
    return newParam;
  }
  private initCompParameter(): CompParameter {
    switch (this.compType) {
      case 'BEComponent':
        return new BEMethodParameter();
      case 'BEMgrComponent':
        return new BEMgrMethodParameter();
      case 'ValidationComponent':
        return new ValidationParameter();
      case 'DeterminationComponent':
        return new DeterminationParameter();
      case 'VMComponent':
        return new VMMethodParameter();
      default:
        throw new TypeError('当前新建构件方法参数类型为：' + this.compType + '，请检查！');
    }
  }
  /**
   * 创建一个参数数据的载体，用于在列表控件中操作
   * @param dataItem 当前参数数据
   */
  private createParamFormGroup(dataItem: any): FormGroup {
    if (dataItem.ParamType === ParameterType.Custom) {
      return this.formBuilder.group({
        'ID': [dataItem.ID, Validators.required], // 不显示，ID
        'ParamCode': [dataItem.ParamCode, Validators.required],
        'ParamName': [dataItem.ParamName, Validators.required],
        'Mode': [dataItem.Mode, Validators.required],
        'ParamType': [dataItem.ParamType, Validators.required],
        'Assembly': [dataItem.Assembly, Validators.required],
        'ClassName': [dataItem.ClassName, Validators.required],
        'ParamDescription': dataItem.ParamDescription,
      });
    } else {
      return this.formBuilder.group({
        'ID': [dataItem.ID, Validators.required], // 不显示，ID
        'ParamCode': [dataItem.ParamCode, Validators.required],
        'ParamName': [dataItem.ParamName, Validators.required],
        'Mode': [dataItem.Mode, Validators.required],
        'ParamType': [dataItem.ParamType, Validators.required],
        'ParamDescription': dataItem.ParamDescription,
      });
    }
  }
  //#region 按钮操作
  /**
   * 按钮操作：新增参数
   * @param sender The `GridComponent` instance
   */
  private addHandler({ sender }: AddEvent) {
    const newRow = this.createNewParameter();
    this.paramGroup = this.createParamFormGroup(newRow);
    this.parameters.push(this.paramGroup.value); // 用于在最后一行新增
    this.editedRowIndex = this.parameters.length - 1; // 定位到最后一行
    sender.editCell(this.editedRowIndex, 0, this.paramGroup); // 使新增行的第一列的单元格设为焦点
  }
  /**
   * 按钮操作：删除选中的参数
   * @param sender The `GridComponent` instance
   */
  private removeHandler() {
    if (this.editedRowIndex === undefined) {
      alert('请先选中要删除的参数行！');
    } else {
      this.parameters.splice(this.editedRowIndex, 1); // 删除指定参数行数据
    }
  }
  //#endregion 按钮操作

  /**
   * 鼠标点击事件：使列表的单元格处于焦点状态时的处理（只点击列表cell时触发）
   * @param sender The `GridComponent` instance
   * @param rowIndex 当前处于焦点的单元格所在行号
   * @param columnIndex 当前处于焦点的单元格所在列号
   */
  private cellClickHandler({ sender, rowIndex, columnIndex }: CellClickEvent) {
    this.editedRowIndex = rowIndex;
    // 获取当前焦点单元格所在的当前参数数据
    const dataItem = this.parameters[rowIndex];
    this.paramGroup = this.createParamFormGroup(dataItem);
    // 在编辑模式下，将焦点放置到指定单元格
    sender.editCell(rowIndex, columnIndex, this.paramGroup);
  }
  /**
   * 事件：焦点离开处于当前列表单元格时进行的操作
   * 1、保证当前单元格中编辑的数据被保存
   * @param param0 当前处于焦点的列表cell的数据和状态
   */
  private cellCloseHandler({ sender, rowIndex, column }: CellCloseEvent) {
    // dirty:脏数据，UI界面已经修改当前数据
    if (this.paramGroup && this.paramGroup.dirty) {
      // 获取当前单元格的值
      const curCellValue = this.paramGroup.value[column.field];
      switch (column.field) {
        case 'ParamCode':
          this.parameters[rowIndex].ParamCode = curCellValue;
          break;
        case 'ParamName':
          this.parameters[rowIndex].ParamName = curCellValue;
          break;
        case 'Mode':
          this.parameters[rowIndex].Mode = curCellValue;
          break;
        case 'ParamType':
          this.parameters[rowIndex].ParamType = curCellValue;
          this.parameters[rowIndex].Assembly = this.getParamAssembly(curCellValue);
          this.parameters[rowIndex].ClassName = this.getParamClassName(curCellValue);
          break;
        case 'Assembly':
          this.parameters[rowIndex].Assembly = curCellValue;
          break;
        case 'ClassName':
          this.parameters[rowIndex].ClassName = curCellValue;
          break;
        case 'ParamDescription':
          this.parameters[rowIndex].ParamDescription = curCellValue;
          break;
      }
    }
    // 关闭当前单元格，清空信息
    this.clearEditInfo(sender);
  }
  //#endregion
  //#region 保存
  /**
   * 保存事件
   */
  private saveClick() {
    if (this.checkParamsValid()) {
      // 对方法参数集合修改后，通过paramsResult返回到外部
      this.paramsResult.next(this.parameters);
    }
  }
  /**
   * 检查参数集合各个字段是否满足要求
   */
  private checkParamsValid(): Boolean {
    // this.parameters.forEach((param, rowIndex) => {
    //   const paramGroup = this.createParamFormGroup(param);
    //   if (paramGroup && !paramGroup.valid) {
    //     alert('构件方法参数集合中，第' + rowIndex + '行的参数中尚有字段不满足条件，请查看！');
    //     return;
    //   }
    // });
    let rowIndex = 0;
    for (const param of this.parameters) {
      rowIndex = rowIndex + 1;
      const paramGroup = this.createParamFormGroup(param);
      if (paramGroup && !paramGroup.valid) {
        alert('构件方法参数集合中，第' + rowIndex + '行的参数中尚有字段不满足条件，请查看！');
        return false;
      }
    }
    return true;
  }
  //#endregion

  /**
   * 关闭编辑状态
   * @param grid 列表控件
   */
  private clearEditInfo(grid: GridComponent) {
    grid.closeCell();
    this.editedRowIndex = undefined;
    this.paramGroup = undefined;
  }
}



