import { Component, OnInit } from '@angular/core';
import { ComponentService } from './component.service';
import { GSPComponent } from '../assets/libs/BaseEntity/GSPComponent';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

export class AppComponent implements OnInit {
  component: GSPComponent;
  // strComponent: string;
  constructor(private compService: ComponentService) { }
  ngOnInit(): void {
    this.getComponent();
  }
  getComponent(): void {
    this.compService.getComponent().subscribe(component => this.component = component);
    // this.strComponent = JSON.stringify(this.component);
  }
}



