import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { BEComponent } from '../assets/libs/BEComponent/BEComponent';
import { ComponentExample } from '../assets/demo/componentExample';

@Injectable()
export class ComponentService {

  constructor() { }

  getComponent(): Observable<BEComponent> {
    return of(ComponentExample as BEComponent);
  }

  public save(param: any, isNew: boolean): void {
    if (isNew) {
      ComponentExample.Method.Parameters.splice(0, 0, param);
    } else {
      Object.assign(ComponentExample.Method.Parameters.find(({ ID }) => ID === param.ID), param);
    }
  }

  public remove(paramIDSelection: any[]): void {
    paramIDSelection.forEach(paramID => {
      const index = ComponentExample.Method.Parameters.findIndex(({ ID }) => ID === paramID);
      ComponentExample.Method.Parameters.splice(index, 1);
    });
  }
}



